import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"

import { Map, Marker, TileLayer } from "react-leaflet"



class IssTracker extends React.Component {
  constructor() {
    super();
    const position = [51.505, -0.09]
    const zoom = 7

    // const issIcon = L.icon({
    //   iconUrl: "/images/issImg.png",
    //   iconSize: [30,30]
    // })

    this.state = {
      position: position,
      zoom: zoom,
      
    }
  }

  componentDidMount() {
    this.interval = setInterval(() => {

      fetch(`https://api.wheretheiss.at/v1/satellites/25544`)
        .then(res => res.json())
        .then(issJson => {
          const isspos = [issJson.latitude, issJson.longitude];
          const zoom = this.map.leafletElement._zoom ?? 7
          this.setState({
            position: isspos,
            zoom: zoom
          })
        }
        )
    }, 1000);
  }

  render() {
    return (
      <Layout>
        <SEO title="ISS" />
        <Map style={{ height: "500px" }} center={this.state.position} zoom={this.state.zoom} ref={(ref) => { this.map = ref }}>
          <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          /> 
          <Marker position={this.state.position} /*icon={this.state.icon}*/>
        </Marker>
        </Map>
       
      </Layout>
    )
  }
}

export default IssTracker


